@supports (display: grid) {

  .grid {
    display: grid;
    grid-gap: .5vw;
    min-height: 100vh;
    padding: 1.5vw;
  }


  @media screen and (min-width: 130px) {
    .grid {
      grid-template-columns: repeat(7, 1fr);
      grid-template-rows: 0.8fr 0.2fr;
      grid-template-areas:
      "keyboard   keyboard   keyboard     keyboard   keyboard    keyboard   keyboard"
      "solution   solution   solution     solution   solution    solution     solution"
      ;
    }
  }

  .grid .item:nth-child(1)  { grid-area: keyboard; }
  .grid .item:nth-child(2) { grid-area: solution; }
}

